.youtube-premium {
  position: absolute;
  left: 0;
  animation: anim-banner 1s 2s ease-in-out forwards;
  top: -106px;
  height: 300px;
  width: 700px;
  opacity: 0;
  visibility: hidden;

  background-color: #0f0f0f;
  box-shadow: 0px 0px 12px 0px rgba(255, 255, 255, 0.23);

  &__content {
    padding: 20px;
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__img {
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #ff0000;
    margin-right: 20px;
  }

  &__right {
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 15px 0;
  }

  &__text {
    font-size: 14px;
    font-weight: 500;

    span:first-child {
      display: block;
      margin-bottom: 15px;
    }

    span:last-child {
      color: #aaa;
    }
  }

  &__links {
    position: relative;
    display: flex;
    justify-content: space-between;

    &::before {
      content: '';
      position: absolute;
      top: -10px;
      left: -20px;
      right: 0;
      height: 1px;
      background-color: rgba(170, 170, 170, 0.4);
    }
  }

  &__link {
    text-transform: uppercase;
    font-weight: 500;
  }

  &__link--skip {
    color: #aaa;
  }

  &__link--buy {
    color: #3ea6ff;
  }
}


@keyframes anim-banner {
  0% {
    transform: translateX(-1000px);
  }

  100% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
}
