@media (max-width:1560px) {
  .youtube-premium {
    height: 202px;
    width: 508px;
  }
}

@media(max-width: 1366px) {
  .video__clip {
    display: none;
  }

  .sidebar__btn {
    padding: 7px 10px;
  }

  .sidebar__descr {
    flex-direction: column;
    align-items: flex-start;
  }

  .sidebar__descr-inner {
    margin-bottom: 10px;
  }

  .sidebar__item-inner img {
    width: 125px;
    height: 125px;
  }

  .header__micro {
    margin-right: 15px;
  }

  .video {
    width: 67%;
  }

  .sidebar {
    width: 31.5%;
  }
}

@media(max-width: 1200px) {
  .video__save {
    display: none;
  }

  .sidebar-desc {
    display: none;
  }

  .video {
    width: 100%;

  }

  .sidebar-mob {
    width: 100%;
    padding-top: 0;
    margin-bottom: 30px;
  }

  .sidebar__descr {
    flex-direction: row;
    align-items: flex-start;
  }

  .video__comments-avatar {
    width: 40px;
    height: 40px;
  }

  .video__comments-label {
    width: 93%;
  }

  .video__comments-header {
    margin-bottom: 10px;
  }

  .video__progress {
    width: 95%;
  }
}

@media(max-width: 992px) {
  .features__inner {
    grid-template-columns: repeat(auto-fit, minmax(312px, 1fr));
    grid-gap: 50px;
  }

  .features__item {
    grid-column: 1 / 3;
  }
}


@media(max-width: 768px) {
  .header__add {
    display: none;
  }

  .header__notif {
    display: none;
  }

  .search-header__input {
    display: none;
  }

  .header__search {
    max-width: unset;
    width: auto;
  }

  .search-header__btn {
    width: 40px;
    height: 40px;
    margin-right: 0;
    border-radius: 0;
    border: 0;
    background-color: transparent;
    box-shadow: 0;
  }

  .header__micro {
    width: 40px;
    height: 40px;
    border-radius: 0;
  }

  .header__left {
    flex-grow: 1;
  }

  .header__right {
    flex-grow: 0;
  }

  .youtube-premium {
    height: 150px;
    width: 400px;
  }

  .youtube-premium__content {
    padding: 10px;
    height: 100%;
  }

  .youtube-premium__img {
    width: 30%;
    height: 73%;

    svg {
      width: 50px;
      height: 50px;
    }
  }

  .youtube-premium__link {
    font-size: 14px;
  }

  .youtube-premium__right {
    padding: 13px 0;
  }

  .video__name {
    max-width: 200px;
  }

  .video__progress {
    width: 90%;
  }

  .controls-video__btn--right svg {
    width: 38px;
    height: 38px;
  }

  .controls-video__right {
    margin-right: 15px;
  }

  .hero-premium__title {
    font-size: 28px;
    max-width: 100%
  }

  .modal__congratulation {
    font-size: 18px;
  }
}


@media(max-width: 540px) {
  .youtube-premium {
    height: 130px;
    width: 350px;
  }

  .youtube-premium__content {
    padding: 10px;
    height: 100%;
  }

  .youtube-premium__img {
    width: 25%;
    height: 65%;

    svg {
      width: 40px;
      height: 40px;
    }
  }

  .youtube-premium__link {
    font-size: 12px;
  }

  .youtube-premium__right {
    padding: 10px 0;
  }

  .controls-video__btn--right svg {
    width: 30px;
    height: 30px;
  }

  .controls-video__btn svg {
    width: 30px;
    height: 30px;
  }

  .controls-video__autoplay::before {
    top: 2px;
    left: -3px;
    height: 16px;
    width: 17px;
  }

  .controls-video__autoplay {
    width: 24px;
  }


  .video__like,
  .video__dislike,
  .video__btn {
    padding: 4px 10px;
  }

  .video__about {
    flex-direction: column;
  }

  .video__right-btns {
    display: block;
  }

  .video__likes {
    width: 130px;
    margin-bottom: 10px;
  }

  .video__share {
    width: 90px;
    margin-bottom: 10px;
  }

  .video__options {
    width: 40px;
  }

  .video__wrapper {
    padding-bottom: 100%;
  }

  .video__controls {
    bottom: 8px;
  }

  .controls-video__autoplay {
    display: none;
  }

  .controls-video__cinema {
    display: none;
  }

  .controls-video__miniplayer {
    display: none;
  }

  .hero-premium__title {
    font-size: 25px;
    margin-bottom: 30px;
  }
  .hero-premium {
    padding-top: 100px;
  }
  .hero-premium__price {
    font-size: 18px;
  }
  .features__name {
    font-size: 22px;
  }
  .features__item p {
    font-size: 14px;
  }

  .feature__content p {
    font-size: 18px;
  }
  .feature__title {
    font-size: 40px;
  }
}


@media screen and (max-height: 600px) {
  .youtube-premium {
    top: -181px;
    width: 438px;
  }

  .youtube-premium__img svg {
    width: 100px;
    height: 100px;
  }
}

@media screen and (max-height: 600px) and (max-height: 768px) {
  .youtube-premium {
    top: -181px;
    height: 195px;
    width: 510px;
  }
}

@media screen and (max-height: 1024px) and (max-height: 960px) {
  .youtube-premium {
    height: 202px;
    width: 608px;
  }
}

@media screen and (max-height: 1440px) and (max-height: 960px) {
  .youtube-premium {
    top: -170px;
    height: 195px;
    width: 510px;
  }
}

@media screen and (max-height: 2560px) and (max-height: 1228px) {
  .youtube-premium {
    top: -170px;
    height: 195px;
    width: 510px;
  }
}




@media(max-width: 450px) {
  .youtube-premium {
    height: 140px;
    width: 303px;
    top: -112px;
  }
}
