html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  background-color: #0f0f0f;
  color: #f1f1f1;
  font-size: 14px;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: transparent;

}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #6e6e6e;
  border-radius: 9em;
  padding: 3px;
}


.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgb(116, 116, 116);
  margin-right: 10px;
  flex: none;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.svg {
  width: 24px;
  height: 24px;
  margin-right: 5px;
}

.mr-0 {
  margin-right: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.main {
  display: flex;
  max-width: 1760px;
  margin: 0 auto;
  padding: 0 20px;
  justify-content: space-between;
}


.plug {
  height: 20px;
  width: 100%;
  border-radius: 7px;
  background-color: #343334;
}

.plug-mini {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #343334;
}

.btn {
  background-color: #3ea6ff;
  font-size: 14px;
  border-radius: 25px;
  padding: 11px 14px;
  font-weight: 500;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
