.premium {
  color: #000;
}

.hero-premium {
  background-color: #fff;
  padding: 160px 10px 48px;

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  &__logo {
    margin-bottom: 50px;
  }

  &__title {
    margin: 0;
    font-family: 'Roboto', 'Arial', sans-serif;
    font-weight: 300;
    font-size: 32px;
    max-width: 535px;
    text-align: center;
    margin-bottom: 40px;
  }

  &__btn {
    margin-bottom: 30px;
  }

  &__price {
    font-weight: 300;
    font-size: 24px;
    margin-bottom: 20px;
  }

  &__info {
    margin-bottom: 25px;
    text-align: center;

    p {
      margin: 0;
      font-size: 16px;
      font-weight: 300;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__link {
    font-weight: 300px;
    font-size: 14px;
    margin-bottom: 20px;

    a {
      color: #3ea6ff;
    }
  }

  &__more {
    font-weight: 300px;
    font-size: 14px;

    a {
      color: #3ea6ff;
      font-weight: 500;
    }
  }
}

.features {
  padding: 64px 10px;

  &__inner {
    display: grid;
    grid-template-columns: repeat(3, 312px);
    align-items: flex-start;
    justify-content: center;
    grid-gap: 10px;
  }

  &__item {
    text-align: center;
    color: #fff;

    img {
      margin-bottom: 15px;
    }

    p {
      margin: 0;
      font-size: 16px;
      color: #aaa;
    }
  }

  &__name {
    font-size: 32px;
    font-weight: 300;
    margin: 0;
    margin-bottom: 5px;
  }
}


.feature {
  min-height: 550px;
  padding: 24px;
  background-position: center top;
  background-size: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: #fff;

  &__content {
    width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: left;

    p {
      margin: 0;
      color: #ffffffcc;
      opacity: 0.7;
      font-size: 20px;
      max-width: 500px;
    }


  }

  &__title {
    font-size: 48px;
    font-weight: 300;
    margin: 0;
    margin-bottom: 20px;
    max-width: 500px;
    width: 100%;
  }

  &:nth-child(even) .feature__content {
    align-items: flex-start;
  }
}


.faq {
  padding: 96px 10px 52px;

  &__container {
    max-width: 952px;
    width: 100%;
    margin: 0 auto;
  }

  &__content {
    margin-bottom: 80px;
    color: #f1f1f1;

    &-item {
      border-bottom: 1px solid #272727;

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  &__title {
    margin: 0;
    font-size: 24px;
    font-weight: 300;
    margin-bottom: 40px;
  }

  &__button {
    font-size: 18px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    svg {
      width: 24px;
      height: 24px;
      fill: #f1f1f1;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    color: #aaa;

    max-height: 0;
    overflow: hidden;
    margin-top: 11px;
    margin-bottom: 7px;

    transition: .3s ease-in-out;

    span {
      display: block;
      font-weight: 500;
      margin-bottom: 8px;
    }

    ul li {
      margin-bottom: 8px;
    }
  }

  &__footer {
    font-size: 18px;
    color: #f1f1f1;
    font-weight: 500;

    a {
      color: #3ea6ff;
    }
  }
}
