.video {

  &__comments-header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    span {
      display: block;
      margin-right: 10px;
      font-size: 16px;
      font-weight: 500;
      margin-right: 30px;
    }
  }

  &__comments-btn {
    display: flex;
    align-items: center;
    color: #f1f1f1;
    font-weight: 500;
    font-size: 14px;
  }

  &__comments-add {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
  }

  &__comments-avatar {
    width: 40px;
    height: 40px;
    background-color: rgb(177, 70, 21);
    margin-right: 20px;
    flex: none;
  }

  &__comments-label {
    font-size: 14px;
    color: #aaa;
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    width: 93%;
  }
}

.comments-list {

  &__item {
    display: flex;
    margin-bottom: 30px;
  }

  &__avatar {
    margin-right: 15px;
    flex: none;
  }

  &__author {
    margin-bottom: 8px;
    font-size: 13px;
    font-weight: 500;

    span:last-child {
      color: #aaa;
      font-weight: 400;
    }
  }

  &__text {
    margin-bottom: 10px;

    p {
      margin: 0;
      font-size: 14px;

      span {
        color: #3ea6ff;
        cursor: pointer;

        @media(min-width: 992px) {
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }

  &__stat {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #aaa;
    margin-bottom: 15px;
  }

  &__like {
    display: flex;
    align-items: center;
    margin-right: 10px;
    cursor: pointer;
  }

  &__dislike {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__reply {
    color: #f1f1f1;
    font-size: 12px;
    margin-left: 20px;
    cursor: pointer;
  }

  &__replies {
    cursor: pointer;

    span {
      position: relative;
      display: flex;
      align-items: center;
      color: #3ea6ff;
      font-size: 14px;
      font-weight: 500;

      &::before {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 7px 7px 0 5px;
        border-color: #3ea6ff transparent transparent transparent;
        margin-right: 12px;
      }

    }
  }
}
