.graph-modal__container {
  width: 100%;
  max-width: 600px;
  border-radius: 0;
  padding-top: 25px;
}

.graph-modal__close {
  font-size: 37px;
  left: 29px;
  top: 15px;
  right: unset;
  font-weight: 300;
}

.modal {

  &__header {
    font-size: 20px;
    color: #212121;
    font-weight: 500;
    padding-left: 48px;
    margin-bottom: 30px;
  }

  &__good {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

  }

  &__good-left {
    display: flex;
  }

  &__good-img {
    margin-right: 10px;

    img {
      width: 56px;
      height: 56px;
    }
  }

  &__good-text {

    span {
      display: block;
      font-size: 14px;
      color: #212121;

      &:first-child {
        margin-bottom: 5px;
        font-weight: 500;
      }

      &:last-child {
        color: #616161;
        font-size: 12px;
      }
    }
  }

  &__good-right {
    font-size: 14px;
    color: #212121;
    font-weight: 500;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 8px;
    margin-bottom: 15px;

  }

  &__info-left {
    span {
      display: block;
      font-size: 14px;
      color: #212121;
    }
  }

  &__info-right {
    font-size: 14px;
    color: #212121;
    font-weight: 500;
  }

  &__payment {
    margin-bottom: 40px;

    span {
      display: block;
      font-size: 14px;
      color: #212121;
      font-weight: 300;
      margin-bottom: 20px;
    }

    label {
      font-size: 14px;
      color: #212121;
      display: inline-block;
      cursor: pointer;
      position: relative;
      padding-left: 35px;
      font-weight: 500;

      &:before {
        content: "";
        display: inline-block;
        width: 20px;
        height: 20px;
        position: absolute;
        left: 0;
        bottom: -1px;
        border-radius: 50%;
        border: 2px solid #bdbdbd;
        background-color: transparent;

      }

      &::after {
        content: '';
        display: none;
        position: absolute;
        bottom: 4px;
        left: 5px;
        background-color: #4285f4;
        width: 10px;
        height: 10px;
        border-radius: 50%;
      }
    }
  }

  &__payment-input {
    display: none;

    &:checked+label:before {
      border-color: #4285f4;
    }

    &:checked+label:after {
      display: block;
    }
  }

  &__footer {
    width: 100%;
  }

  &__btn {
    width: 100%;
    padding: 10px 0;
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 14px;
    background-color: #065fd4;
    border-radius: 4px;
  }
}


.modal {

  &__form {
    width: 100%;
  }

  &__form-text {
    display: block;
    font-size: 14px;
    color: #212121;
    margin-bottom: 40px;
  }

  &__form-card {
    position: relative;
    z-index: 1;
    margin-bottom: 60px;

    img {
      position: absolute;
      left: 0;
      top: -5px;
    }

    label {
      padding-left: 52px;
    }

    input {
      padding-left: 52px;
    }
  }

  &__form-input {
    width: 100%;
    padding-bottom: 4px;
    border: none;
    border-bottom: 1px solid #e0e0e0;
    background-color: transparent;
    transition: .3s;

    &:focus {
      outline: none;
      border-bottom: 1px solid #065fd4;
    }
  }

  &__form-placeholder {
    position: absolute;
    top: 0;
    left: 0;
    color: #616161ff;
    font-size: 17px;
    transition: .3s;
    z-index: -1;
  }

  &__form-card--info {
    display: flex;
    justify-content: space-between;
    gap: 25px;
    margin-bottom: 39px;
  }

  &__form-group {
    position: relative;
    z-index: 1;
    width: 100%;
  }

  &__form-card--name {
    position: relative;
    z-index: 1;
    margin-bottom: 30px;
  }

  &__form-info {

    .modal__form-group {
      margin-bottom: 30px;
    }
  }

  &__form-country {
    margin-bottom: 30px;

    &-text {
      font-size: 12px;
      color: #616161;
      margin-bottom: 5px;
    }

    &-name {
      font-size: 16px;
      color: #212121;
      font-weight: 500;
    }
  }

  &__form-terms {
    font-size: 12px;
    color: #616161;

    a {
      color: #065fd4;
      ;
    }

    margin-bottom: 20px;
  }

  &__form-start {
    width: 100%;
  }

  &__congratulation {
    font-size: 26px;
    font-weight: 500;
    text-align: center;
  }
}


.modal__form-input:focus+label,
.modal__form-input:not(:placeholder-shown)~label {
  top: -18px;
  font-size: 12px;
  color: #065fd4;
}

.form-prompt {
  flex: none;

  &__btn {
    width: 100%;

    img {
      width: 26px;
      height: 26px;
      opacity: .8;
    }
  }
}


.prompt-modal {
  position: absolute;
  width: 280px;
  height: 253px;
  background-color: #fff;
  border-radius: 5px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 27px 0px rgba(0, 0, 0, 0.5);
  z-index: 100;

  &__content {
    padding: 25px;

    img {
      width: 82px;
      height: 65px;
      display: flex;
      margin: 0 auto;
      margin-bottom: 35px;
    }
  }

  &__title {
    text-align: left;
    color: #212121;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 25px;
  }

  &__text {
    color: #212121;
    font-size: 14px;
    margin-bottom: 15px;
  }

  &__footer {
    width: 100%;
    text-align: right;
  }

  &__btn {
    text-transform: uppercase;
    font-size: 14px;
    color: #065fd4;
    font-weight: 700;
  }
}


.modal__form-input.just-validate-error-field {
  border-color: #d50000;
}

.modal__form-input.just-validate-error-field+label {
  color: #d50000;
}

.just-validate-error-label,
.my-error {
  font-size: 12px;
  color: #d50000;
  margin-top: 5px;
}

.modal__form-btn:disabled {
  pointer-events: none;
  opacity: .7;
}

.graph-modal:before {
  height: auto;
}

.modal__info-date {
  display: flex;
  align-items: center;

  span {
    color: #616161 !important;
    font-size: 12px !important;
  }
}

#month {
  display: block;
  margin-left: 4px;
  margin-right: 3px;
}

#year {
  display: block;
  margin-left: 4px;
}
