.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #0f0f0f;
  height: 56px;


  // height: 56px;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__menu {
    width: 30px;
    height: 30px;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;

    span {
      position: relative;
      width: 18px;
      height: 1px;
      display: block;
      background-color: #fff;

      &::after,
      &::before {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #fff;
      }

      &::after {
        bottom: -6px;
      }

      &::before {
        top: -6px;
      }
    }
  }

  &__logo {
    display: flex;

    svg {
      width: 90px;
      height: 20px;
    }
  }

  &__search {
    position: relative;
    display: flex;
    align-items: center;
    max-width: 728px;
    width: 100%;
  }

  &__btn {


    svg {
      width: 24px;
      height: 24px;
      fill: #fff;
    }
  }

  &__micro {
    width: 50px;
    height: 40px;
    background-color: #181818;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__right {
    display: flex;
    align-items: flex-end;
  }

  &__add {
    margin-right: 25px;
  }

  &__notif {
    margin-right: 25px;
  }

  &__user {
    border-radius: 50%;
    width: 32px;
    height: 32px;
    background-color: rgb(177, 70, 21);
  }
}

.search-header__input {
  align-items: center;
  caret-color: #f1f1f1;
  margin-left: 32px;
  padding: 0px 4px 0px 16px;
  background-color: #121212;
  border: 1px solid #303030;
  border-right: none;
  border-radius: 40px 0 0 40px;
  box-shadow: inset 0 1px 2px #00000000;
  color: rgba(255, 255, 255, 0.88);
  height: 40px;
  width: 100%;

  &:focus {
    outline: none;
    border-radius: 0;
  }

  &::placeholder {
    font-weight: 500; 
  }
}

.search-header__btn {
  width: 78px;
  height: 40px;
  margin-right: 8px;
  border-radius: 0 40px 40px 0;
  border: 1px solid #303030;
  background-color: rgba(255, 255, 255, 0.08);
  box-shadow: 0 1px 0 rgb(0 0 0 / 10%);
}

.search-header__block {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  align-items: center;
  background-color: #121212;
  border: 1px solid #303030;
  border-right: none;
  border-radius: 40px 0 0 40px;
  box-shadow: inset 0 1px 2px #00000000;
  color: rgba(255, 255, 255, 0.88);
  padding: 2px 7px;
  height: 40px;

  svg {
    width: 20px;
    height: 20px;
    fill: #fff;
    margin-left: 8px;
  }
}

.search-header__input:focus~.search-header__block {
  display: flex;
  outline: none
}
