.video {

  &__about {
    position: relative;
    display: flex;
  }

  &__left {
    width: 48%;
  }

  &__name {
    max-width: 500px;
    margin-bottom: 13px;
  }

  &__plug {
    max-width: 300px;
  }

  &__descr {
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.15);
    padding: 12px;
    margin-bottom: 22px;
    width: 100%;

    &-info {
      display: block;
      font-size: 14px;
      color: #f1f1f1;
      font-weight: 500;
      margin-bottom: 5px;
    }

    &-text {
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #f1f1f1;

      p {
        margin: 0;
        margin-right: 5px;
      }
    }
  }

  &__right {
    width: 52%;

    &-btns {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      margin-bottom: 30px;
    }
  }

  &__likes {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 13.5px;
    background-color: #343334;
    border-radius: 20px;

    svg {
      width: 24px;
      height: 24px;
    }

    cursor: pointer;

    &:not(:last-child) {
      margin-right: 10px;

      svg {
        margin-right: 5px;
      }
    }
  }

  &__like {
    display: flex;
    align-items: center;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    padding: 6px 13px;
    border-radius: 20px 0 0 20px;
    width: 100%;
    height: 100%;

    &:hover {
      background-color: #535353;
    }
  }

  &__dislike {
    display: flex;
    padding: 6px 13px;
    border-radius: 0 20px 20px 0;

    &:hover {
      background-color: #535353;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 13.5px;
    background-color: #343334;
    padding: 6px 13px;
    border-radius: 20px;
    margin-right: 10px;

    svg {
      width: 24px;
      height: 24px;
      margin-right: 5px;
    }

    cursor: pointer;

    &:hover {
      background-color: #535353;
    }
  }

  &__options {
    margin-right: 0;
    padding: 6px;

    svg {
      margin-right: 0;
    }
  }
}

.author-video {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-bottom: 15px;

  &__about {
    display: flex;
  }

  &__text {}

  &__name {
    font-weight: 500;
    margin-bottom: 5px;
  }

  &__subscribers {
    font-size: 12px;
    color: #aaa;
  }

  &__subscribe {
    color: #fff;
    background-color: #c00;
    border-radius: 20px;
    font-size: 14px;
    padding: 10px 16px;
    font-weight: 500;
    cursor: pointer;
  }
}
