.sidebar {
  padding-top: 77px;
  width: 27.5%;
}

.sidebar {

  &__adv {
    border: 1px solid #464646;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 15px;
  }

  &__ad-img {
    img {
      border-radius: 5px 5px 0 0;
    }
  }

  &__descr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;

    &-inner {
      display: flex;
      align-items: center;
    }
  }

  &__descr-img {
    width: 48px;
    height: 48px;
    margin-right: 10px;

    img {
      border-radius: 45px;
    }
  }

  &__descr-text {
    display: flex;
    flex-direction: column;
    align-items: start;


    span:first-child {
      margin-bottom: 5px;
    }

    span:last-child {
      font-size: 12px;
      color: #fff;
      font-weight: 700;

      a {
        color: #aaa;
      }
    }
  }
}

.sidebar {

  &__item {
    margin-bottom: 10px;
    cursor: pointer;
  }

  &__item-inner {
    display: flex;

    img {
      width: 158px;
      height: 158px;
      border-radius: 10px;
      margin-right: 10px;
    }
  }

  &__item-text {
    font-size: 14px;
    overflow: hidden;
  }

  &__item-name {
    font-weight: 500;
    margin-bottom: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__item-descr {
    color: #aaa;
    font-size: 12px;
    margin-bottom: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.sidebar__descr-text--item {
  margin-bottom: 10px;
}
