.video {
  width: 71%;

  &__wrapper {
    position: relative;
    padding-bottom: 62%;
    z-index: -1;

    margin-bottom: 20px;
  }

  &__block {
    position: absolute;
    top: 77px;
    left: 0;
    bottom: 0;
    right: 0;
    // width: 50%;
    background-color: #000;
    // min-height: 720px;
    // height: 100%;
    z-index: 1;

    box-shadow: 60px 0 120px 80px rgba(255,255,255,0.03);
  }

  &__progress {
    position: absolute;
    bottom: 52px;
    left: 20px;
    width: 97%;
    background-color: rgb(106 106 108 / 30%);
    height: 3px;
  }

  &__controls {
    position: absolute;
    bottom: -3px;
    left: 18px;
    z-index: 2;
  }

}

.controls-video {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 97%;

  &__btn {
    svg {
      width: 40px;
      height: 40px;
    }

    &--right {
      svg {
        width: 42px;
        height: 42px;
      }

      &:not(:last-child) {
        margin-right: 6px;
      }
    }
  }

  &__next {
    svg {
      width: 48px;
      height: 48px;
    }
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
  }

  &__autoplay {
    position: relative;
    width: 36px;
    height: 14px;
    background-color: #b5b4b4;
    border-radius: 15px;
    margin-right: 10px;
    margin-bottom: 3px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 19px;
      width: 19px;
      border-radius: 20px;
      margin-top: -3px;
      background-color: #727271;
      background-image: url("../img/pause.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
}

.time-video {
  color: #fff;
  font-size: 13px;
  margin-left: 10px;
}
